body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.check{
  width:30px;
}
.table{
  margin-bottom: 0px;
}
.table>tbody>tr>td{
  vertical-align: middle;
}
input[type=checkbox]{
  margin: 0px;
  -webkit-transform: scale(1.2,1.2);
      -ms-transform: scale(1.2,1.2);
          transform: scale(1.2,1.2);
}
.options .form-control{
  width: auto;
  display: inline-block;
}
.col-xs-12{
  padding-right: 0px;
}
.options{
  padding:5px 0px 10px;
  margin: 0px;

}
.navbar{
  border-radius:0px;
  margin-bottom:0px;
}
.box-body{
  width: 100%;
  height: 100%;
  /* overflow:scroll; */
}
.box-title{
  display: inline-block;
}
.box-header span{
  padding-top:15px;
}
.navbar-inverse{
  background-color: #2196F3;
  border-color: #2196F3;
}
.navbar-inverse .navbar-nav>li>a{
  color: white;
}


